.row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.formHeader {
  text-align: center;
}

.email,
.password {
  display: block;
  width: 100%;
  height: 32px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 4px;
  padding: 8px;
  border: solid 1px #d4d4d4;
}

input[type="email"],
input[type="password"] {
  background-color: #fff;
  outline: none;
}

input[type="checkbox"] {
  margin-right: 8px;
}

.text {
  display: block;
  font-size: 12px;
  margin-top: 24px;
}

.link {
  margin-left: 8px;
}

.error {
  border: 2px solid red;
}



